@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: 'Press Start 2P', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input[type="text"]{
    font-size: 16px;
    font-family: 'Press Start 2P', cursive;
    text-align: center;
    width: auto;
    padding: 5px;
    border-radius: 10px;
    margin: 10px 20px;
}
.containerInput::before{
    content: '.';
    font-size: 30px;
    background-color: green;
    color: green;
    border-radius: 15px;
    position: relative;
    left: 20px;
    animation: parpadeo infinite ease-out 1s alternate;
}


@keyframes parpadeo {
    0%{
        background-color: rgb(52, 94, 52);
        color: rgb(52, 94, 52);
        transform: scale(.5);
    }
    100%{
        background-color: rgb(0, 255, 0);
        color: rgb(0, 255, 0);
        transform: scale(1);
    }
}
.ImagePoke{
    width: 260px;
    border-radius: 15px 15px 0px 0px;
    background-color: white;
}
.ContainerImg{
    background-color: gray;
    padding: 30px;
    margin: 20px 20px 20px 20px;
}
.stats{
    width: 100%;
    background-color: white;
    border-radius: 0px 0px 15px 15px;
    padding: 5px;
    font-size: 10px;

}
.NamePoke{
    text-align: center;
    margin:30px 0;
}

.ButtonChange{
    font-family: 'Press Start 2P', cursive;
    font-size: 20px;
    padding: 5px;
    background-color: rgba(71, 68, 131, 0.939);
    border-radius: 25px;
    height: 50px;
    border: none;
    box-shadow: 0px 5px black;
}
.Botonera{
    width: 300px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}
.ButtonChange:hover{
    transform: translateY(5px);
    cursor: pointer;
    color: black;
    box-shadow: 0px 0px black;
}
.App{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: red;
  margin: 10px 0px;
  padding: 20px 0px;
  border-radius: 40px 0 40px 0;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
}
.MessageError{
  width: 80%;
  font-size: 10px;
}
